.custom-card {
    background-color: #007aff;
    color: #fff;
    border-radius: 10px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    margin-bottom: 20px;
  }
  
  .custom-card:hover {
    transform: scale(1.03);
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  }
  
  .card-content {
    text-align: center;
  }
  
  .card-title {
    margin-bottom: 8px;
  }